<template>
  <div class="call-statistics">
    <div class="form-container">
      <TabList
        :list="tabList"
        :activeIndex="activeIndex"
        @emitTab="tabClick"
      ></TabList>
      <div class="search-box">
        <el-form :model="paramsData" class="form">
          <el-form-item label="公司名称：">
            <el-input
              v-model="paramsData.companyName"
              placeholder="请输入"
              clearable
            />
          </el-form-item>
          <el-form-item label="公司代码：">
            <el-input
              v-model="paramsData.companyCode"
              placeholder="请输入"
              clearable
            />
          </el-form-item>
          <el-form-item label="分配状态：">
            <el-select
              placeholder="请选择"
              v-model="paramsData.distributeState"
              @change="getList"
              clearable
              style="width: 200px"
            >
              <el-option
                v-for="item in shareStatusTypes"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="margin-left: 20px" @click="search"
              >搜索</el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="table-container">
      <!--   列表   -->
      <el-table :data="pageData.list" height="calc(100vh - 260px)">
        <el-table-column
          align="center"
          prop="companyName"
          key="companyName"
          label="公司名称"
        />
        <el-table-column
          align="center"
          prop="companyCode"
          key="companyCode"
          label="公司代码"
        />
        <el-table-column
          align="center"
          prop="unusedNum"
          key="unusedNum"
          label="待分配数"
        />
        <el-table-column
          align="center"
          prop="assignedNum"
          key="assignedNum"
          label="下发数"
        />
        <el-table-column
          align="center"
          prop="orderNum"
          key="orderNum"
          label="订购数"
        />
        <el-table-column
          align="center"
          prop="distributeState"
          key="distributeState"
          label="分配状态"
        >
          <template #default="{ row }">
            <!-- 分配状态 （0:未分配,1:已分配） -->
            {{ row.unusedNum > 0 || row.orderNum === 0 ? "待分配" : "已分配" }}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="createdTime"
          key="createdTime"
          label="最近申请时间"
        />
        <el-table-column align="center" label="操作">
          <template #default="{ row }">
            <el-button type="text" @click="viewInfo(row)">查看详情</el-button>
          </template>
        </el-table-column>
        <template #empty>
          <TableEmpty empty-text="暂无数据"></TableEmpty>
        </template>
      </el-table>
      <!--   分页   -->
      <PagePagination
        :pageTotal="pageData.pageTotal"
        :pageSize="pageData.pageSize"
        :pageNumber="pageData.pageNumber"
        @emitPageSizeChange="pageSizeChange"
        @emitPageNumberChange="pageNumberChange"
      ></PagePagination>
    </div>
  </div>
</template>
<script lang="ts" setup>
import { ref, shallowReactive } from "vue";
import { LocalStorage, usePage } from "jz-tool-lib";
import { orderListApi } from "@api/phone-num-order";
import { useVueRouter } from "@assets/useHook";

const tabList = [
  {
    label: "虚拟号",
    value: "2",
  },
  {
    label: "固话",
    value: "4",
  },
  {
    label: "实体卡",
    value: "5",
  },
  {
    label: "虚拟主叫",
    value: "7",
  },
];

const shareStatusTypes = [
  {
    label: "待分配",
    value: "0",
  },
  {
    label: "已分配",
    value: "1",
  },
  {
    label: "全部",
    value: "-1",
  },
];
const { goPage } = useVueRouter();
const { bd_userInfo: userInfo } = LocalStorage.batchGetItem(["bd_userInfo"]);
const activeIndex = ref("2"); // 审核状态
const paramsData = shallowReactive({
  numberType: "2",
  companyCode: "",
  companyName: "",
  distributeState: "-1",
  parentId: userInfo.profileId,
});

const { getList, pageNumberChange, pageSizeChange, pageData, resetPageNumber } =
  usePage({
    listApi: orderListApi,
    props: {
      pageSize: "pageSize",
      pageNumber: "pageNo",
    },
    searchParams: () => {
      return {
        ...paramsData,
      };
    },
  });

const tabClick = (value) => {
  activeIndex.value = value.value;
  paramsData.numberType = value.value;
  resetPageNumber();
  getList();
};

const viewInfo = (row) => {
  goPage({
    path: "/businessManagement/ospreyCloudCall/number-ordering/detail",
    query: row,
  });
};

const search = () => {
  resetPageNumber();
  getList();
};
</script>
<style scoped lang="scss">
.search-box {
  @include flex(center, center);

  .el-input {
    margin-right: 10px;
  }

  :deep(.el-range-editor.el-input__wrapper) {
    width: 260px;
    margin-right: 10px;
  }
  .label,
  .text {
    font-size: 14px;
    color: #606266;
  }
  .form {
    display: flex;

    .el-form-item--large.el-form-item {
      margin-bottom: unset;
    }
  }
}
</style>
